.wrapperGlobal {
  min-height: 100vh;
  background-image: url(assets/img/background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 0;
}

.wrapper {
  padding-top: 40px;
  height: calc(100vh - 40px);
  overflow: scroll;
}

.inner {
  max-width: 1300px;
  margin: 0 auto;
  padding-bottom: 100px;
}

.title {
  text-align: center;
  font-size: 110px;
  font-family: "AmaticSC-Bold";
  color: #333;
}
